.our-mission{
    background: #037c21;
    margin-top: -21px;
    height: 1000px;
    width: 100%;
}

.mission-header{
    display: flex;
    text-align: center;
    justify-content: center;
    background: white;
    height: 108px;
    width: 760px;
    margin-left: 11rem;
    border-radius: 12px;
    box-shadow: 0 2px 10px #000;
    margin-top: -5rem;
}

.mission-header h2{
    font-size: 1.86rem;
   margin-top: 10px;
   color: #037c21;
   font-family: 'Inter',sans-serif;
   font-weight: 900;
}

.mission-cards{
    display: flex;
   flex-wrap: wrap;
    justify-content: center;
    text-align: center;
    gap: 50px;
    margin-top: 5rem;
 
}

.mission{
    width: 455px;
    height: 240px;
    background: #fff;
    border-radius: 23px;
    margin-top: 5rem;
    box-shadow: 0 2px 12px #000;
}

.mission .img{
    width: 53px;
    height: 53px;
    max-width: 53px;
    max-height: 53px;
    padding: 20px;
    background: #fff;
    border-radius: 53px;
    border: 2px solid #037c21;
    margin-top: -3rem;
    fill: #037c21;
   
}

.mission h4{
    font-size: 1.4rem;
    color: #037c21;
    font-family: 'poppins', sans-serif;
    font-weight: 900;
    margin-bottom: 3px;
}


.mission p{
    font-size: 16px;
    font-family: 'poppins', sans-serif;
    font-weight: 540;
    padding: 17px;
    margin-top: -4px;
    color: #037c21;
}


/* Outreach  section */
.mission .outreach-p{
    font-size: 13.8px;
    margin-top: -5px;
}