.hero-section{
    display: flex;
    flex-direction: column;
   
}

.hero-header{
 margin-left: 5.8rem;
 margin-top: 4rem;
 font-size: 2.3rem;
 font-family: 'Inter', sans-serif;
 color: #d8bb16;
}

.hero-paragraph{
    color: white;
    font-family: 'Inter', sans-serif;
    font-size: 13px;
    margin-left: 5.8rem;
    margin-top: 2px;
    line-height: 1.45;
    letter-spacing: .5px;
}

.btn{
    display: flex;
    flex-direction: row;
    gap: 20px;
    margin-left: 6.7rem;
    margin-top: 2rem;
}

.btn .learn-more{
    height: 38px;
    width: 150px;
    border-radius: 9px;
    border: none;
    outline: none;
    background: #037c21;
    color: white;
    align-items: center;
    font-size: 14px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}

.arrow-right{
    margin-left: 10px;
    height: 1.3rem;
    width: 1.3rem;
    align-items: center;
    fill: none;
    stroke: #fff;
    stroke-width: "2";
}

.btn .support-us{
    height: 38px;
    width: 150px;
    border-radius: 9px;
    border: none;
    outline: none;
    background: transparent;
    border: 1px solid white;
    color: white;
    align-items: center;
    font-size: 14px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}

.btn  .dollar{
    margin-left: 10px;
    height: 1.3rem;
    width: 1.3rem;
    align-items: center;
    fill: none;
    stroke: #fff;
    stroke-width: "2";
}