.impact{
    width: 100%;
    height: 1000px;
    margin-top: 9rem;
    background: #fff;
    border-top-right-radius: 16px;
    border-top-left-radius: 16px;
}


.header h1{
    text-align: center;
    color: #037c21;
    font-family: 'Inter', sans-serif;
    font-weight: 900;
    padding-top: 60px;
}

.impact-cards{
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: space-between;
    padding: 20px;
}

 .card-farmers{
    height: 280px;
    width: 410px;
    background: linear-gradient(103deg, rgba(3, 163, 62, 0.85)45%, rgba(86, 171, 47, 0)
    ),url('../images/ex-healthy.jpeg') no-repeat no-repeat;
    background-size: cover;
    background-position: center;
    box-sizing: border-box;
    box-shadow: 0 2px 8px rgba(0,0,0,0.4);
    border-radius: 22px;
    margin-top: 1.5rem;
    margin-left: 70px;

}



.card-farmers h2{
    text-align: center;
    font-family: 'poppins', sans-serif;
    font-size: 2rem;
    font-weight: 900;
    color:#fff;
    margin-top: 8.35rem;
}

.card-farmers p{
    color: #fff;
    text-align: center;
    font-family: 'inter', sans-serif;
    font-size: 1.2rem;
    padding-left: 28px;
    padding-right: 28px;
    margin-top: -16px;
}

