@import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap');


.our-people{
    background: #010f24;
    height: 650px;
}


.brainers-title{
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
}

.brainers-title h2{
    margin-top: 3rem;
    font-size:19px;
    font-weight: 700;
    font-family: 'Inter',sans-serif;
    background: #fff;
    height: 50px;
    width: 230px;
    border-radius: 12px;
    color: #010f24;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}



.our-brains-section{
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-top: 5rem;
}

.ibrahim-section{
    display: flex;
    flex-wrap: wrap;
    gap: 25px;
    margin-left: 4rem;
  
}

 img{
    height: 140px;
    width: 140px;
    max-width: 140px;
    max-height: 140px;
    border-radius: 50%;
    background-position: center;
    background-size: cover;
}

.left-details{
    display: flex;
    flex-direction: column;
  margin-top: -1rem;
}

.left-details h3{
    color:#fff;
    font-size: 20px;
    font-family: 'Inter',sans-serif;

   
}

.left-details span{
    font-size: 14px;
    color: #E1E143;
    font-family: 'Inter', sans-serif;
    margin-top: -.9rem;
    position: relative;

}

.left-details span::after{
    content: "";
position: absolute;
left: 0;
top:  35px;
height: 3px;
width: 230px;
background: #E1E143;

}


.left-details p{
    margin-top: 2.3rem;
    color: white;
    font-family: 'poppins', sans-serif;
    font-size: 14px;
    font-weight: 300;
    justify-content: center;
  line-height: 1.3;
}

.agents-section{
    margin-left: 11rem;
    display: flex;
    gap: 25px;
}

.kenny-section{
    margin-top: 3rem;
    margin-left: 5rem;
    display: flex;
    gap: 25px;
}