.Awards-section{
    background: #fff;
    margin-top: -1.7rem;
    width: 100%;
    height: 650px;
   
}

.award-header h2{
    display: flex;
    justify-content: center;
    padding-top: 8rem;
    font-size: 32px;
    font-weight: 900;
    font-family: Arial, sans-serif;
    color: #062553;
}

.award-tag{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 80px;
    margin-top: 7rem;
}

.d-prizes{
    display: flex;
    flex-direction: column;
   
}
.d-prizes .dprize{
    background: url('../images/dprize.jpeg');
    height: 180px;
    width: 380px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    border: 4px solid #062553;
    border-radius: 12px;
    box-shadow: 0 2px 12px #000;
   
}

.d-prizes p{
    font-family: 'Inter', sans-serif;
    font-size: 17px;
    color: #032d6d;
    text-align: start;
    margin-left: 2rem;
    margin-top: 1.5rem;
}

.clim-accelerator{
    display: flex;
    flex-direction: column;
}

.climmAcc{
    background: url('../images/climeAcc.png');
    height: 180px;
    width: 380px;
    background-repeat: no-repeat no-repeat;
    background-size: cover;
    background-position: center;
    border: 4px solid #062553;
    border-radius: 12px;
    box-shadow: 0 2px 12px #000;
}

.clim-accelerator p{
    font-family: 'Inter', sans-serif;
    font-size: 17px;
    color: #032d6d;
    text-align: start;
    margin-left: 3rem;
    margin-top: 1.5rem;
}