
@import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap');


.nav-header{
   height: 82px;
   width: 900px;
   border-radius: 14px;
   margin-left: 5.9rem;
   margin-top: 2rem;
   z-index: 202;
   background: white;
   display: flex;
   align-items: center;
   position: relative; 
}

.nav-header img{
    width: 139.13px;
    height: 41px;
    margin-left: 40px;
    margin-top: 4px;
}

 .nav-link ul{
    display: flex;
    flex-direction: row;
    gap: 1.5rem;
    margin-left: 30px;
    text-align: center;
    align-items: center;
}

.nav-link ul li{
  list-style: none;
  font-size: 14px;
  font-family: sans-serif;
  color: #037c21;
}

.nav-link ul li a{
    text-decoration: none;
    color: #037C3E;
    cursor: pointer;
    position: relative;
}


.nav-link ul li a::after{
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: -6px;
    left: 0;
    background-color: #037c21;
    transform: scaleX(0);
    transform-origin: bottom center;
    transition: transform 0.3s ease-out;
}

.nav-link ul li a:hover::after {
    transform: scaleX(1);
  
  }
  
  /* For the active link, you might want the underline to show permanently */
.search-container{
    width: 100%;
    max-width: 240px;
}  

.search{
    position: relative;
    margin-left: 2.5rem;
    height: 35px;
    width: 240px;
    background: transparent;
    border: 1px solid #037c21;
    border-radius: 6px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    
}

.search input{
    width: 100%;
    border-radius: 4px;
    border: none;
    padding: 16px;
    color: #037C3E;
    font-size: 14px;
    outline: none;
    background: transparent;
}

.search .search-icon{
    margin-right: 16px;
    color: #037c21;
    height: 25px;
    width: 25px;
}

.contact-us{
    height: 34px;
    width: 110px;
    border-radius: 7px;
    background: #037c21;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    margin-left: 4rem;
    font-size: 12px;
    font-family: "Inter", sans-serif;
    font-style: normal;
    cursor: pointer;transition: all .5s ease;

}

.contact-us:hover{
    scale: 1.2;
    background: #E1E143;
    color: #037c21;
   
}